.preloader{
    height:100vh;
    width:100%;
    background:#000;
    color:#FFF;
    position:fixxed;
    bottom:0;
    left:0;
    right:0;
    z-index:55;
    display:flex;
    justify-content:center;
    align-items:center;
    overflow:hidden;
}
.preloader .texts-container{
    display:flex;
    align-items:center;
    justify-content:space-between;
    height:60px;
    width:280px;
    font-size:20px;
    font-weight:800;
    overflow:hidden;
    color:#FFF;
}